import { User } from "@/redux/reduxTypes";
import axios from "axios";


export const SignUp = async (pass: string, name: string): Promise<User|void> => {
try{
const res = await axios.post("http://localhost:5200/api/auth/register",{name,pass},{withCredentials:true});
if(!res)return;
return res.data;
}catch(err){
    console.error(err);
}
}
export const LogIn = async (pass: string, name: string): Promise<User|void> => {
try{
const res = await axios.post("http://localhost:5200/api/auth/login",{name,pass},{withCredentials:true});
if(!res)return;
return res.data;
}catch(err){
    console.error(err);
}
}